
body {
  margin: 0px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  font-size: 0px;
  background-color: #ffff00;
}
h1 {
  margin: 0px;
  padding: 0px;
}
a {
  text-decoration: none;
  cursor: pointer;
}
#app {
  /* border: 1px solid #000; */
  position: absolute;
  width: 100%;
}
#sidebar,
#content {
  font-size: 18pt;
}
#sidebar {
  position: fixed;
  width: 250px;
  height: 100%;
  background-color: #151719;
  left: -250px;
  -webkit-transition: left 500ms linear;
  transition: left 500ms linear;
}
#sidebar.active {
  left: 0px;
}
#sidebar ul {
  list-style: none;
  padding: 0px;
  margin: 0px;
}
#sidebar ul li {
  color: rgba(230, 230, 230, 0.9);
  padding: 15px 10px;
  border-bottom: 1px solid rgba(100, 100, 100, 0.3);
}
#sidebar ul li a {
  color: #ffffff;
}
#sidebar ul li a + ul {
  display: none;
}
#sidebar ul li a.active + ul {
  display: block;
}
#sidebar .toggle-btn {
  position: absolute;
  left: 265px;
  top: 8px;
  background-color: #ffff00;
  padding: 0px 5px;
}
#sidebar .toggle-btn span {
  display: block;
  width: 30px;
  height: 5px;
  background-color: #151719;
  margin: 5px 0px;
}
#content {
  padding-top: 50px;
  min-width: calc(100% -  270px);
  margin-left: 250px;
  padding-right: 10px;
  padding-left: 10px;
  text-align: justify;
  margin-left: 0px;
}
@media screen and (min-width: 600px) {
#content {
    -webkit-transition: margin-left 500ms linear;
    transition: margin-left 500ms linear;
}
#content.active {
    margin-left: 250px;
}
}

